import { Component, OnInit } from '@angular/core';
import {CookieService} from '../../../shared/services/cookie.service';
import {IntegrationService} from '../../../shared/services/integration.service';
import {environment} from '../../../../environments/environment';
import { Router } from '@angular/router';
declare var jQuery: any;
@Component({
    selector: 'app-og-mailchimp-redirect',
    styleUrls: ['./mailchimp-redirect.component.css'],
    templateUrl: './mailchimp-redirect.component.html'
})
export class MailChimpRedirectComponent implements OnInit {
    mailChimpError: string;
    ismailChimpError: boolean = false;
    constructor(private _cookieService: CookieService, private _integrationService: IntegrationService, private router: Router) { }

    ngOnInit() {
        this.getAuthorizationCode();
     }

     getAuthorizationCode() {
         let companyId = this._cookieService.readCookie('comp');
         let authcode = decodeURIComponent(window.location.search.split('code=')[1]);
         console.log(authcode,'codeeeeeeeeeeeeeeeeeeeeeee');
         let data = {
             authcode: authcode,
         };
         this._integrationService.authorization(data, 'mailchimp' , companyId)
            .subscribe((result) => {
                console.log('result in -----------------------------auth', result);
                this._cookieService.createCookie('comp', 'success', 3);
            }, (error) => {
                this._cookieService.eraseCookie('comp');
                this.ismailChimpError = true;
                this.mailChimpError = error.error.err_message;
                jQuery('#mailchimp-error').modal('show');
            });
     }

     close() {
         window.close();
     }
}



